<template>
  <div class="mobile">
      <router-view />
  </div>
</template>

<script>
export default {
  components: {},
  props: {},
  data() {
    return {
    };
  },
  watch: {},
  computed: {
    device_type(){
      return this.$store.state.app.device_type
    }
  },
  methods: {},
  created() {
    if(this.device_type!='mobile'){
      this.$router.push('/')
    }
  },
  mounted() {}
};
</script>
<style lang="scss" scoped>
.mobile{
    width: 100%;
    height: 100%;
}
</style>


